.img-icon {
  display: flex;
  position: relative;
  top: 21px;
  right: 29px;
}

.button-adv {
  width: 178px;
  height: 36px;
  margin-top: 32px;
  margin-bottom: 29px;
  border-color: #2d74da;
  color: #2d74da;
  border-radius: 3px;
  background: #ffffff;
  object-fit: contain;
  border: solid 1px #2d74da;
  font-family: SourceSansPro;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #2d74da;
}

.button-delete {
  border-color: #fa5e5b;
  color: #fa5e5b;
  border: solid 1px #fa5e5b;
  margin: 14px 0 0 0;
}

.button-adv:hover {
  background-color: #2d74da;
  color: #ffffff;
  cursor: pointer;
}

.button-delete:hover {
  background-color: #fa5e5b;
  cursor: pointer;
}

.button-adv:active {
  background-color: #1c5bb5;
}

.button-delete:active {
  background-color: #fa4643;
  color: #ffffff;
}

.button-edit {
  margin: 0;
}

.font-adv {
  font-family: SourceSansPro;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.57;
  letter-spacing: normal;
  color: #68717c;
}

.title-adv {
  font-size: 18px;
  word-wrap: break-word;
  line-height: 1.22;
  color: #1b2834;
  margin-bottom: 2px;
}

.description-adv {
  max-width: 315px;
  word-wrap: break-word;
  margin-bottom: 7px;
}

.right-row-adv-item {
  display: flex;
  flex-direction: column;
}

.img-adv {
  width: 332px;
  max-height: 250px;
}

.adv-item-container {
  display: flex;
  flex-direction: row;
  margin-bottom: 78px;
}

.left-row-adv-item {
  display: flex;
  flex-direction: column;
  width: 332px;
  margin-right: 103px;
}

.adv-buttons {
  margin-top: auto;
  width: 178px;
}
